.container {
    background-color: #FFEFD8;
    background-color: rgba(255, 239, 216, 0.26);
    clear: both;
    padding-top: 35px;
    padding-bottom: 70px;
}

.content {
    width: 1250px;
    position: relative;
    left: calc((100vw - 1250px) / 2);
}

.header_container {
    text-align: center;
}

.header_container > h2 {
    margin-top: 20px;
    word-spacing: 2px;
    margin-bottom: 105px;
    font-weight: 500;
}

#info_container {

    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

#info_container > img {
    margin-top: 20px;
}

.box_description {
    line-height: 32px;
}

.box_header {
    margin-bottom: 0px;
}

.learn_button_container {
    width: 100%;
}
